<template>
  <div class="alert-charge">
    <div class="charge-mask">
      <div class="modal-header">
        <p><img @click="chargeDele" src="../../assets/images/room/gc_16.png"/></p>
      </div>
      <div class="modal-body">
        <div class="service">
          <ul>
            <li>
              <p><img src="../../assets/images/thebar/bc_33.png"/><a>QQ客服</a></p>
              <div>QQ群:544486687</div>
              <div>客服QQ1:486546063</div>
              <div>客服QQ2:3117407163</div>
            </li>
            <li>
              <p><img src="../../assets/images/thebar/bc_32.png"/><a>电话客服</a></p>
              <div>客服电话:400-158-2058</div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .alert-charge{
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .charge-mask{
    position: relative;
    width: 60%;
    height: n(500);
    background: #f4f4f4;
    margin-left: 20%;
    margin-top: 20%;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    border-radius: 15px;
    overflow: hidden;
    .modal-header{

      width: 100%;
      height: n(70);
      p{
        width: 100%;
        height: 100%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: relative;
        img{
          position: absolute;
          left: 10px;
          width: n(30);
          height: n(30);
        }
        a{
          font-size: 14px;
        }
      }
    }
    .modal-body{
      width: 70%;
      margin-left: 15%;
      .service{
        ul{
          li{
            p{
              width: 100%;
              height: n(60);
              display: inline-flex;
              align-items: center;
              justify-content: center;
              background:#fff001;
              margin-bottom: n(10);
              margin-top: n(10);
              img{
                width: n(40);
                height: n(40);
              }
            }
            div{
              width: 100%;
              text-align: center;
            }
          }
        }
      }
    }
  }
</style>
<script>


    export default {
        data() {
            return {
            }
        },
        components: {

        },
      methods:{
        chargeDele:function () {
          this.$emit('childSay');
        },
      }
    }
</script>



