<template>
    <div class="noticeBody">
      <Service v-show="serviceShow" v-on:childSay="servicehide"></Service>
      <router-view></router-view>
      <div class="user-header">
        <a class="nav-item1" @click="backHoom"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
        <a class="gucang">通知中心</a>
        <a @click="lian" class="nav-item2">联系客服</a>
      </div>
      <div class="noticeMain">
        <div class="tabs">
          <div class="t1" @click="system"><a>系统消息</a></div>
          <div class="t2" @click="customer"><a>客服回复</a></div>
        </div>

        <ul v-show="systemShow">
          <div v-show="one" style="margin-top: 10px">暂无系统消息!</div>
          <li v-for="(item,index) in sysList" @click="noticeBtn($event,item,index)" @touchmove="move" @touchstart="start">
            <div class="mainH">
              <p class="titleL"><a :class="item.is_read == 1 ? '': 'visit'"></a><span>系统消息</span></p>
              <p class="titleR">
                <a>{{item.created_at}}</a>
                <img src="../../assets/images/usercenter/gc_28.png"/>
              </p>
            </div>
            <div class="mainR">
              <a>{{item.content}}</a>
            </div>
            <div class="delete" @click.stop="deleteNotice(item,index)">删除</div>
          </li>
          <div v-show="loadShow" class="load" @click="jiazai"><a>加载更多</a></div>
        </ul>

        <ul v-show="customerShow">
          <div v-show="two" style="margin-top: 10px">暂无客服消息!</div>
          <li v-for="(item,index) in message" @click="custorBtn($event,item,index)" @touchmove="moveServes" @touchstart="startServes">
            <div class="mainH">
              <p class="titleL"><a :class="item.is_read == 2 ? '': 'visit'"></a><span>客服消息</span></p>
              <p class="titleR">
                <a>{{item.create_time}}</a>
                <img src="../../assets/images/usercenter/gc_28.png"/>
              </p>
            </div>
            <div class="mainR">
              <a>{{item.content}}</a>
            </div>
            <div class="reply"><a @click="replybtn(item)">回复</a></div>
            <div class="deleteServer" @click.stop="deleteServer(item,index)">删除</div>
          </li>
          <div v-show="loadShowduo" class="load" @click="more"><a>加载更多</a></div>
        </ul>
      </div>
      <div class="question-alert" v-show="feedbackShow">
        <div class="question-mask">
          <div class="modal-header">
          </div>
          <div class="modal-body">

            <textarea id="textLines"></textarea>
            <p class="subfeedback" @click="feedbackBtn"><button>提交反馈</button></p>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
  import Service from './service.vue'
  var format = function (timestamp) {
    var date = new Date(timestamp * 1000);
    var Y = date.getFullYear() + '/';
    var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
    var D = date.getDate() + ' ';
    var h = date.getHours() + ':';
    var m = date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes();
    var s = date.getSeconds();
    return Y+M+D;
  }
  var urlhost = process.env.API_AJAX;
  export default {
    name:"notice",
    data() {
      return {
        message:[],
        dian:true,
        startX:0,
        startY:0,
        moveEndX:0,
        moveEndY:0,
        X:0,
        Y:0,
        id:'',
        idx:'',
        i:true,
        serviceShow:false,
        customerShow:false,
        systemShow:true,
        one:false,
        sysList:[],
        two:false,
        feedbackShow:false,
        checkList:[],
        selectArr:[],
        loadShow:false,
        loadShowduo:true,
        notice_id:"",
        page:1,
        morenum:1
      }
    },
    components: {
      Service,
    },
    mounted:function () {
      $(".t1 a").addClass("active");
      var _this = this;
      var user_id = getCookie('userId');
      //系统消息
      $.ajax({
        type: 'GET',
        url: urlhost+"systemlist",
        data$Type: "jsonp",
        data:{
          "user_id":user_id,
          "num":2
        },
        success: function (response) {
          var data = JSON.parse(response);
          console.log(data);
          var sys = JSON.parse(response).message;
          if(data.status == 0){
            _this.one = true;
            _this.loadShow = false;
          }else{
            _this.one = false;
            _this.loadShow = true;
            for(var i=0;i< sys.length;i++){
              var num = parseInt(sys[i].created_at);
              var time = format(num);
              sys[i].created_at = time;
            }
            _this.sysList = sys;
          }
        },
      });
      //客服回复
      $.ajax({
        type: 'GET',
        url: urlhost+"backnotice/noticelist",
        data$Type: "jsonp",
        data:{
          "user_id":user_id,
          "num":10
        },
        success: function (response) {
          var mess = JSON.parse(response);
          var myorder = mess.message;
          if(mess.status == 0){
            _this.two = true;
            _this.loadShowduo = false;
          }else{
            _this.loadShowduo = true;
            _this.two = false;
            for(var i=0;i< myorder.length;i++){
              var num = parseInt(myorder[i].create_time)
              var time = format(num);
              myorder[i].create_time = time;
            }
            _this.message = mess.message
          }
        },
      });
    },
    methods:{
      system:function () {
        $(".t1 a").addClass("active").parent().siblings().children().removeClass("active");
        this.systemShow = true;
        this.customerShow = false;
      },
      replybtn:function (item) {
        var _this = this;
        console.log(item)
        var _this = this;
        _this.notice_id = item.back_notice_id;
        _this.feedbackShow = true;
      },
      customer:function () {
        $(".t2 a").addClass("active").parent().siblings().children().removeClass("active");
        this.systemShow = false;
        this.customerShow = true;
      },
      start:function (e) {
        var _this = this
        _this.startX = e.targetTouches[0].pageX;
        _this.startY = e.targetTouches[0].pageY;
//        e.preventDefault();
      },
      move:function (e) {
        var _this = this;
        _this.moveEndX = e.targetTouches[0].pageX;
          _this.moveEndY = e.targetTouches[0].pageY;
          _this.X = _this.moveEndX - _this.startX;
          _this.Y = _this.moveEndY - _this.startY;
        if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X > 0 ) {
          $(e.currentTarget.children[2]).animate({'width':0},100)
        }
        else if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X < 0 ) {
          $(e.currentTarget.children[2]).animate({'width':'1.5rem'},100)
        }
      },
      deleteNotice:function (item,index) {
          var _this = this;
          var id = item.id;
        $.ajax({
          type: 'GET',
          url: urlhost+"delsystem",
          data$Type: "jsonp",
          data: {
            "system_id":id
          },
          success: function (response) {
            var mess = JSON.parse(response);
            _this.sysList.splice(index, 1);
            $('.delete').css("width",0)
            _this.$layer.msg(mess.message)
          },
        });
      },
      moveServes:function (e) {
        var _this = this;
        _this.moveEndX = e.targetTouches[0].pageX;
        _this.moveEndY = e.targetTouches[0].pageY;
        _this.X = _this.moveEndX - _this.startX;
        _this.Y = _this.moveEndY - _this.startY;
        if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X > 0 ) {
          $(e.currentTarget.children[3]).animate({'width':0},100)
        }
        else if ( Math.abs(_this.X) > Math.abs(_this.Y) && _this.X < 0 ) {
          $(e.currentTarget.children[3]).animate({'width':'1.5rem'},100)
        }
      },
      startServes:function (e) {
        var _this = this;
        _this.startX = e.targetTouches[0].pageX;
        _this.startY = e.targetTouches[0].pageY;
      },
      deleteServer:function (item,index) {
        var _this = this;
        var id = item.id;
        console.log(id)
        $.ajax({
          type: 'GET',
          url: urlhost+"backnotice/replydel",
          data$Type: "jsonp",
          data: {
            "back_notice_id":id
          },
          success: function (response) {
            var mess = JSON.parse(response);
            console.log(mess)
            _this.message.splice(index, 1);
            $('.deleteServer').css("width",0)
            _this.$layer.msg(mess.message)
          },
        });
      },
      backHoom:function () {
        this.$router.go(-1);
      },
      feedbackDele:function () {
        this.feedbackShow = false;
      },
      lian:function () {
        var _this = this;
        _this.serviceShow = true;
      },
      servicehide:function () {
        this.serviceShow = false;
      },
      jiazai:function () {
        var _this = this;
        var user_id = getCookie('userId');
        _this.page++;
        $.ajax({
          type: 'GET',
          url: urlhost+"systemlist",
          data$Type: "jsonp",
          data: {
            "user_id":user_id,
            "page":_this.page,
            "num":2
          },
          success: function (response) {
            var data = JSON.parse(response)
            if(data.status == 0){
              _this.$layer.msg(data.message)
            }else{
              console.log(data.message)
              var list = data.message;
              list.forEach(function (i) {
                _this.sysList.push(i)
              })
            }
          },

        });
      },
      noticeBtn:function (event,item,index) {
          var _this = this;
            var id = item.id;
        $.ajax({
          type: 'GET',
          url: urlhost+"readsystem",
          data$Type: "jsonp",
          data: {
            "system_id":id
          },
          success: function (response) {

          },

        });
            if(_this.i ){
             $(event.currentTarget.children[1].children[0]).css({
               'overflow':'visible',
              'text-overflow':'',
              'white-space':'normal'
             })
              _this.i = false;
            }else{
              $(event.currentTarget.children[1].children[0]).css({
                'overflow':'hidden',
                'text-overflow':'ellipsis',
                'white-space':'nowrap'
              })
              _this.i = true;
            }
        $(event.currentTarget.children[0].children[1].children[1]).toggleClass("trans");
        $(event.currentTarget.children[0].children[0].children[0]).css("visibility","hidden" )
      },
      custorBtn:function (event,item,index) {
        var _this = this;
        var id = item.id;
        if(_this.i ){
          $(event.currentTarget.children[1].children[0]).css({
            'overflow':'visible',
            'text-overflow':'',
            'white-space':'normal'
          })
          _this.i = false;
        }else{
          $(event.currentTarget.children[1].children[0]).css({
            'overflow':'hidden',
            'text-overflow':'ellipsis',
            'white-space':'nowrap'
          })
          $.ajax({
            type: 'GET',
            url: urlhost+"backnotice/readback",
            data$Type: "jsonp",
            data: {
              "reply_id":id
            },
            success: function (response) {

            },

          });
          _this.i = true;
        }
        $(event.currentTarget.children[0].children[1].children[1]).toggleClass("trans");
        $(event.currentTarget.children[0].children[0].children[0]).css("visibility","hidden" )
      },
      more:function () {
        var _this = this;
        var user_id = getCookie('userId');
        _this.morenum++;
        $.ajax({
          type: 'GET',
          url: urlhost+"backnotice/noticelist",
          data$Type: "jsonp",
          data: {
            "user_id":user_id,
            "page":_this.morenum,
            "num":2
          },
          success: function (response) {
            var data = JSON.parse(response)
            if(data.status == 0){
              _this.$layer.msg(data.message)
            }else{
              var listmore = data.message;
              listmore.forEach(function (i) {
                _this.message.push(i)
              })
            }
          },

        });
      },
      feedbackBtn:function () {
        var _this = this;
        var textLines = $("#textLines").val();
        var name = getCookie("name");
        var user_id = getCookie("userId")
        if( textLines == ''){
          _this.$layer.msg("回复内容不能为空")
        }else{
          console.log(_this.notice_id)
          console.log(name)
          console.log(user_id)
          console.log(textLines)
          $.ajax({
            type: 'POST',
            url: urlhost+"backnotice/replynotice",
            data$Type: "jsonp",
            data: {
              "back_notice_id":_this.notice_id,
              "account":name,
              "user_id":user_id,
              "content":textLines
            },
            success: function (data) {
              var obj = JSON.parse(data);
              if(obj.status == 0){
                _this.$layer.msg(obj.message);
              }else{
                _this.$layer.msg(obj.message);
                _this.feedbackShow = false;
              }
            },
          })
        }

      },
    },
    watch: {
      selectArr(val) {
        var _this = this;
        _this.selectArr = val;
        console.log(val)
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .noticeBody{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: #f2f2f2;
    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1{
        .game_1{
          margin-left: n(23);
          width: n(50);
          height: n(50);
        }
      }
      .gucang{
        font-size: 14px;
        font-weight: bold;
      }
      .nav-item2{
        margin-right: n(23);
        font-size: n(22);
      }
    }
    .noticeMain{
      width: 95%;
      margin: auto;
      margin-top: n(80);
      overflow: hidden;
.tabs{
  width: 100%;
  height: n(60);
  display: flex;
  align-items: center;
  background: #ffffff;
  div{
    display: flex;
    justify-content: center;
    height: n(60);

    width: 50%;
    a{
      height: n(60);
      line-height: n(60);

    }
  }
}
      ul{
        li{
          margin-top: 10px;
          width: 100%;
          padding: 5px 10px 5px 10px;
          background: #ffffff;
          position: relative;
          .mainH{
            width: 100%;
            height: n(40);
            display: inline-flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid #f7f7f7;
            .titleL{

              a{
                display: inline-block;
                width: n(15);
                height: n(15);
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                background: #0d8cf5;
                margin-right: 10px;
              }
            }
            .titleR{
              img{
                margin-left: 10px;
                width: n(20);
                height: n(40);
              }
          }
          }
          .mainR{
            width: 100%;
            margin-top: 5px;
            /*word-wrap:break-word;*/
            /*word-break:break-all;*/
            a{
              display: block;
              width: 100%;
              overflow:hidden;
              text-overflow:ellipsis;
              white-space:nowrap;

            }
          }
          .delete{
            position: absolute;
            top:0;
            right: 0;
            height: 100%;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background: #ff4e00;
            color: #ffffff;
            width: n(0);

          }
        }
      }
    }

  }

  .trans{
    transform:rotate(90deg);
    -ms-transform:rotate(90deg); 	/* IE 9 */
    -moz-transform:rotate(90deg); 	/* Firefox */
    -webkit-transform:rotate(90deg); /* Safari 和 Chrome */
    -o-transform:rotate(90deg);
  }
  .transback{
    transform:rotate(0deg);
    -ms-transform:rotate(0deg); 	/* IE 9 */
    -moz-transform:rotate(0deg); 	/* Firefox */
    -webkit-transform:rotate(0deg); /* Safari 和 Chrome */
    -o-transform:rotate(0deg);
  }
  .active{
    border-bottom: 2px solid #fff001;
  }
  .hidden{
    overflow:hidden;/*超出长度的文字隐藏*/
    text-overflow:ellipsis;/*文字隐藏以后添加省略号*/
    white-space:nowrap;/*强制不换行*/
  }
  .reply{
    width: 100%;
    text-align: right;
    a{
      border-radius: 3px;
      padding: 2px;

      border:1px solid #000;
    }
  }
  .question-alert{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
    z-index: 9998;
    .question-mask{
      width: 70%;
      height: n(600);
      background: #f4f4f4;
      margin-left: 15%;
      margin-top: 20%;
      -webkit-border-radius: 15px;
      -moz-border-radius: 15px;
      border-radius: 15px;
      overflow: hidden;
      .modal-header{
        width: 100%;
        height: n(70);
        background: #fff001;
        p{
          width: 100%;
          height: 100%;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          position: relative;
          img{
            position: absolute;
            left: 10px;
            width: n(50);
            height: n(50);
          }
          a{
            font-size: 14px;
          }
        }
      }
      .modal-body{
        width: 100%;
        height: n(545);

        textarea{
          margin-top: n(50);
          border:1px solid #ebebeb;
          width: 80%;
          margin-left: 10%;
          height: n(200);
        }
        .subfeedback{
          width: 100%;
          margin-top: n(80);
          text-align: center;
          button{
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;
            width: n(150);
            height: n(50);
            border:0;
            outline: 0;
            background: #fff001;
          }
        }
      }
    }
  }

  .load{
    margin-top: 20px;
    width: 100%;
    a{
      display: block;
      text-align: center;
      font-size: n(22);
      border:1px dashed #422b1f;
      color: #422b1f;
      height: n(40);
      line-height: n(40);
      width: n(120);
      margin: auto;
    }
  }
  .deleteServer{
    position: absolute;
    top:0;
    right: 0;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #ff4e00;
    color: #ffffff;
    width: n(0);
  }
  .visit{
    visibility: hidden;
  }
</style>




